.auth {
  width: 100vw;
  height: 100vh;

  &__background_image {
    width: 100%;
    height: 100%;
    background-image: url('../assets/images/background.png');
    background-position: center;
    background-repeat: no-repeat;
    filter: blur(10px);
    opacity: 0.6;
    background-size: auto 100%;
  }
}

.window {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 409px;
  height: 382px;
  background: #fff;
  position: absolute;
  border-radius: 16px;

  &__items {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 32px;
    gap: 32px;

    &_title {
      font-weight: 500;
      font-size: 24px;
      line-height: 28px;
      align-items: center;
      text-align: center;
      color: #424242;
      height: 28px;
    }

    &_inputs {
      display: flex;
      flex-direction: column;
      gap: 24px;

      &-block {
        gap: 7px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        height: 76px;

        span {
          font-weight: 300;
          font-size: 16px;
          line-height: 19px;
          color: #424242;
        }

        &-input {
          width: 345px;
          height: 50px;
          border-radius: 4px;
        }
      }
    }
  }
}


.button {
  width: 345px;
  height: 50px;
  background: linear-gradient(104.34deg, #41522e -15.34%, #be8022 145.95%);
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #fff;

  &:hover {
    background: linear-gradient(0deg, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.2)),
      linear-gradient(104.34deg, #41522e -15.34%, #be8022 145.95%);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  }

  &:active {
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)),
      linear-gradient(104.34deg, #41522e -15.34%, #be8022 145.95%);
  }
}
